<script>
import CountBox from "@/components/CountBox.vue";
import {mapActions, mapGetters, mapMutations, mapState} from "vuex";

export default {
  name: "Cart",
  components: {CountBox},
  data() {
    return {
      isEdit: false // 是否编辑状态
    }
  },
  created() {
    this.getCartList() // 获取购物车
  },
  computed: {
    ...mapState('cart', ["cartList"]), // 购物车商品列表
    // 购物车商品总数, 被选择的购物车商品总数, 被选择的购物车商品总价 和 被选择的购物车商品列表
    ...mapGetters('cart', ["cartTotal", "selectedCartTotal", "selectedCartPrice", 'selectedCartList']),

    isAllChecked() { // 全选框是否被选中
      return this.cartTotal === this.selectedCartTotal
    }
  },
  methods: {
    ...mapActions('cart', ['getCartList', 'updateCart', 'clearCart']),
    ...mapMutations('cart', ['TOGGLE_SMALL_CHECKED', 'TOGGLE_All_CHECKED']),

    // 切换小选框
    onToggleSmallChecked(id) {
      this.TOGGLE_SMALL_CHECKED(id);
    },

    // 切换全选框
    onToggleAllChecked(isAllChecked) {
      this.TOGGLE_All_CHECKED(isAllChecked);
    },

    // 删除购物车被选中的商品
    async onClearCart() {
      // 被选中商品为 0 直接return
      if (!this.selectedCartTotal) return

      // 这里为什么用await，因为 clearCart 是一个 async 函数，默认返回一个 promise
      // 当这个 async 函数执行成功的时候，promise 才会成功，await 才会往下走
      await this.clearCart()

      this.isEdit = false // 删除商品后关闭编辑模式
    },

    // 跳转支付页
    onGoCheckout() {
      if (this.selectedCartTotal <= 0) return

      this.$router.push({
        path: '/checkout',
        query: {
          mode: 'cart',
          cartIds: this.selectedCartList.map(item => item.id).join(',')
        }
      })
    }
  }
}
</script>

<template>
  <div class="cart">

    <van-nav-bar title="购物车"/>

    <div v-if="cartTotal > 0">
      <!-- 购物车开头 -->
      <div class="cart-title">

        <span class="all">共<i>{{ cartTotal }}</i>件商品</span>

        <span @click="isEdit = !isEdit" class="edit">
        <van-icon name="edit"/>
        编辑
      </span>

      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">

        <div class="cart-item" v-for="item in cartList" :key="item.goods_id">

          <van-checkbox @click="onToggleSmallChecked(item.goods_id)" :value="item.isChecked"></van-checkbox>

          <div @click="$router.push(`/product/${item.goods_id}`)" class="show">
            <img :src="item.goods.goods_image" alt="">
          </div>

          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <div class="bottom">
              <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>

              <!--修改购物车商品 num 是要更新商品数量的值，obj是要更新商品的数据，也就是一个商品对象-->
              <CountBox
                  @input="(num) => updateCart({num, goods_id: item.goods_id, goods_sku_id: item.goods_sku_id})"
                  :value="item.goods_num"
              ></CountBox>
            </div>
          </div>

        </div>

      </div>

      <div class="footer-fixed" style="bottom: 50px;">

        <div @click="onToggleAllChecked(isAllChecked)" class="all-check">
          <van-checkbox
              :value="isAllChecked"
              icon-size="18"
          ></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{ selectedCartPrice }}</i></span>
          </div>
          <!--如果 selectedCartTotal 为 0，那说明没有被选中的商品，那按钮自然得变成灰色-->
          <div
              v-if="!isEdit"
              :class="{disabled: !selectedCartTotal}"
              @click="onGoCheckout"
              class="goPay"
          >
            结算({{ selectedCartTotal }})
          </div>
          <div v-else @click="onClearCart" :class="{disabled: !selectedCartTotal}" class="delete">删除</div>
        </div>

      </div>
    </div>

    <!--空购物车-->
    <div v-else style="height: calc(100vh - 96px)" class="empty-cart">
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        您的购物车是空的, 快去逛逛吧
      </div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>

  </div>

</template>

<style scoped lang="less">
// 主题 padding
.cart {
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;

  .van-nav-bar {
    position: sticky;
    top: 0;
    width: 100%;
  }

  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;

    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }

    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    height: 120px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show {
      display: flex;
      align-items: center;

      img {
        width: 80px;
        height: 80px;
      }
    }

    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;

        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;

          span {
            font-size: 16px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  font-size: 14px; // 文字也会响应式

  .all-check {
    display: flex;
    align-items: center;

    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    align-items: center;

    .price {
      font-size: 14px;
      margin-right: 10px;

      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;

      &.disabled {
        background-color: #ff9779;
      }
    }
  }
}

.empty-cart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  img {
    width: 140px;
    height: 92px;
  }

  .tips {
    margin: 30px 0;
    color: #666;
  }

  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
  }
}
</style>
