<script>
import GoodsItem from "@/components/GoodsItem.vue";
import {getHomeDataApi} from "@/api/home";
import SearchInput from "@/components/searchInput.vue";

export default {
  name: "Home",
  components: {SearchInput, GoodsItem},
  async created() {
    const {data: {pageData: {items}}} = await getHomeDataApi()
    this.swipeList = items[1].data
    this.navList = items[3].data
    this.productList = items[6].data
    // console.log(items)
  },
  data() {
    return {
      swipeList: [],
      navList: [],
      productList: [],
    }
  }
}
</script>

<template>
  <div class="home" style="padding-bottom: 50px;">
    <header>
      <!-- 导航条 -->
      <van-nav-bar title="智慧商城"/>

      <SearchInput></SearchInput>
    </header>

    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in swipeList" :key="item.imgUrl">
        <img :src="item.imgUrl" alt="">
      </van-swipe-item>
    </van-swipe>

    <!-- 导航 -->
    <van-grid column-num="5">
      <van-grid-item
          v-for="item in navList" :key="item.imgUrl"
          :icon="item.imgUrl"
          :text="item.text"
          @click="$router.push('/category')"
      />
    </van-grid>

    <!-- 主会场 -->
    <div class="main">
      <img src="@/assets/main.png" alt="">
    </div>

    <div class="guess">
      <p class="guess-title">—— 猜你喜欢 ——</p>

      <div class="goods-list">
        <GoodsItem v-for="item in productList" :key="item.goods_id" :item="item"></GoodsItem>
      </div>
    </div>

  </div>
</template>

<style scoped lang="less">
.home {
  background-color: #f6f6f6;

  header {
    position: sticky;
    top: 0;
    z-index: 999;
    width: 100%;

    // 导航条样式定制
    ::v-deep .van-nav-bar {
      background-color: #c21401;

      .van-nav-bar__title {
        color: #fff;
      }
    }
  }

  // 分类导航部分
  .my-swipe .van-swipe-item {
    height: 185px;

    img {
      width: 100%;
      height: 185px;
    }
  }

  // 主会场
  .main img {
    display: block;
    width: 100%;
  }

  // 猜你喜欢
  .guess .guess-title {
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
}
</style>
