import axios from "axios";
import {Toast} from "vant";
import store from "@/store";

// 创建一个 axios 实例
const axiosInstance = axios.create({
  baseURL: 'http://smart-shop.itheima.net/index.php?s=/api', // 基础 URL
  timeout: 5000, // 请求超时时间
  headers: {
    platform: 'H5', // 每一次请求都会带上这个参数
  }
});

// 添加请求拦截器
axiosInstance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // console.log(config)

  Toast.loading({
    message: '加载中...',
    forbidClick: true, // 禁用背景点击
    loadingType: 'spinner',
    duration: 0, // 永不关闭
  });

  // 这样子每一次请求都会去获取vuex中getters的token，不能写在上方的create中，那样子只会获取一次vuex的token
  // 后续token更新的话，携带的请求参数token不会更新
  const token = store.getters.token
  if (token) config.headers['Access-Token'] = token

  return config;
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error);
});

// 添加响应拦截器
axiosInstance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  Toast.clear()

  const res = response.data
  if (res.status !== 200) {
    // 前提是后端确实有返回响应错误消息，这样即使 res.response 是 undefined，访问 undefined.message 也不会报错
    Toast(res?.message)
    // 返回 Promise.reject 会打断后续的 then 链
    // 直接进入最近的 catch
    return Promise.reject(res?.message)
  }
  // 正常返回会继续链式调用
  return res
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  return Promise.reject(error);
});

// 导出实例，以便在其他文件中使用
export default axiosInstance
