import Vue from "vue";

Vue.directive('focus', {
  // 当绑定元素插入到 DOM 中。
  inserted: function (el) {
    // 由于 van-field 是一个封装组件，我们需要找到实际的 input 元素
    // 假设 van-field 渲染的 input 元素是其子元素之一
    const inputElement = el.querySelector('input');
    if (inputElement) {
      inputElement.focus();
    } else {
      console.warn('没有找到input元素');
    }
  }
});
