<script>
export default {
  name: "CountBox",
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      timerId: null
    }
  },
  methods: {
    reduce() {
      if (this.value <= 1) return
      this.$emit("input", this.value - 1)
    },
    add() {
      this.$emit("input", this.value + 1)
    },
    handle(e) {
      // 用户第一次输入会打开一个定时器，然后第二次输入马上清掉定时器，只有等用户输入完才会正常打开一个定时器
      clearTimeout(this.timerId)

      this.timerId = setTimeout(() => {

        const num = +e.target.value // num先转数字，空字符串会变为0

        // 异常回退，先判断是不是一个整数，然后判断这个整数是不是负数
        if (!Number.isInteger(num) || num <= 0) return e.target.value = this.value

        // 正常提交
        this.$emit('input', num)

        this.timerId = null
      }, 500)

    }
  },
}
</script>

<template>
  <div class="countBox">
    <div @click="reduce" class="reduce">-</div>
    <input @input="handle" :value="value" type="text">
    <div @click="add" class="add">+</div>
  </div>
</template>

<style scoped lang="less">
.countBox {
  display: flex;

  .reduce, .add, input {
    width: 30px;
    height: 30px;
    line-height: 30px;
    background-color: #efefef;
    text-align: center;
  }

  input {
    background-color: #efefef;
    border: 0;
    margin: 0 5px;
    outline: none;
  }
}
</style>
