import {clearCartApi, getCartListApi, updateCartApi} from "@/api/cart";
import {Toast} from "vant";
import {setCartTotal} from "@/utils/storage";

export default {
  namespaced: true,
  state() {
    return {
      cartList: [], // 购物车商品列表
    }
  },
  mutations: {
    SET_CART_LIST(state, arr) {
      state.cartList = arr
    },

    // 小选框切换状态
    TOGGLE_SMALL_CHECKED(state, id) {
      const obj = state.cartList.find(item => item.goods_id === id)
      obj.isChecked = !obj.isChecked
    },

    // 点击全选框传递全选框当前状态
    // 比如当前全选框未选中，点击后就传递 false，遍历每一个小选框，切换小选框状态为 true，此时全选框也会变为 true
    TOGGLE_All_CHECKED(state, isAllChecked) {
      state.cartList.forEach(item => item.isChecked = !isAllChecked)
    },

    UPDATE_CART(state, {num, goods_id}) {
      const obj = state.cartList.find(item => item.goods_id === goods_id)
      obj.goods_num = num
    }
  },
  actions: {
    // 请求后端cartList数据
    async getCartList(context) {
      const res = await getCartListApi()

      const arr = res.data.list // 拿到请求的cartList数组

      arr.forEach(item => {
        item.isChecked = false
      })

      context.commit('SET_CART_LIST', arr)
    },

    // 更新对应 id 的商品的数量，所以需要拿到对应商品的数量 num ,obj 是 商品id，商品数量，商品SKU
    async updateCart(context, obj) {
      const {num, goods_id, goods_sku_id} = obj

      context.commit('UPDATE_CART', {num, goods_id}) // 前端更新，再更新后端

      await updateCartApi(goods_id, num, goods_sku_id) // 后端更新，这里参数顺序不能错
    },

    async clearCart(context) {
      const selectedCartList = context.getters.selectedCartList
      const cartIds = selectedCartList.map(item => item = item.id) // 把原本数组里的商品对象替换成 id

      await clearCartApi(cartIds) // 更新后端

      // 重新从后端拉取数据更新前端
      // 这最后加个await其实还有个好处，async返回的是promise，最后的这个重新拉取会成功后async再return
      await context.dispatch('getCartList')
      Toast('删除成功')
    }
  },
  getters: {
    // 购物车商品总数
    cartTotal(state) {
      const num = state.cartList.reduce((sum, item) => {
        return sum + item.goods_num
      }, 0)
      setCartTotal(num) // 存入本地存储，虽然这样不合法，但是这是最容易实现的效果
      return  num
    },

    // 被选中的购物车商品列表
    selectedCartList(state) {
      return state.cartList.filter(item => item.isChecked) // 从原数组把那些 isChecked 为 false 的过滤掉
    },

    // 被选中购物车商品的总数
    selectedCartTotal(state, getters) {
      return getters.selectedCartList.reduce((sum, item) => {
        return sum + item.goods_num
      }, 0)
    },

    // 被选中购物车商品的总价
    selectedCartPrice(state, getters) {
      return getters.selectedCartList.reduce((sum, item) => {
        return sum + item.goods.goods_price_min * item.goods_num
      }, 0).toFixed(2)
    }
  }
}
